@use '../../../styles/shared/truncate';

at-sidebar-menuitem {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    border-radius: var(--token-border-radius-md);
    padding: 6px 8px;
    text-align: left;
    cursor: pointer;
    font-size: var(--token-font-size-button);
    transition:
        background-color var(--token-transition-time-fast),
        color var(--token-transition-time-fast),
        box-shadow var(--token-transition-time-fast);

    .at-sidebar-menuitem__content {
        display: flex;
        align-items: center;
        gap: 8px;
        @extend %truncate-text;
    }

    .at-sidebar-menuitem__badge {
        position: absolute;
        top: -8px;
        left: 22px;
        z-index: 20;
        padding: 4px 3px;
        pointer-events: none;
    }

    .at-sidebar-menuitem__label {
        z-index: 10;
        font-weight: var(--token-font-weight-med);
        @extend %truncate-text;
    }

    i.material-icons {
        font-style: normal;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: var(--token-font-size-icon-xl);
        z-index: 10;
    }

    div[data-name='focus-indicator'] {
        pointer-events: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 0;
        background-color: var(--token-sidebar-foreground);
        opacity: 0;
        transition: background-color var(--token-transition-time-fast);
    }

    &:hover div[data-name='focus-indicator'] {
        opacity: 0.1;
    }

    &:focus-visible {
        outline: none;
        box-shadow: 0 0 0 2px
            color-mix(
                in srgb,
                var(--token-state-active-foreground) 60%,
                transparent
            );
    }

    &[data-state='active'] {
        background-color: var(--token-state-active-foreground);

        div[data-name='focus-indicator'] {
            opacity: 0.1;
        }
    }

    &.depth-0 {
        font-weight: var(--token-font-weight-light);
    }

    &.depth-1 {
        padding-left: 36px;
        font-weight: var(--token-font-weight-light);
    }

    &.depth-2 {
        padding-left: 50px;
        font-weight: var(--token-font-weight-light);
    }

    &.depth-3 {
        padding-left: 64px;
        font-weight: var(--token-font-weight-light);
    }
}

[data-name='accordion-item-content']
    at-sidebar-menuitem
    span[data-name='sidebar-menu-item-label'],
at-menu at-sidebar-menuitem span[data-name='sidebar-menu-item-label'] {
    font-weight: 400;
}
